export interface PaginationInterface {
  page: number;
  limit: number;
}

export interface QueryPaginationInterface {
  skip: number;
  take: number;
}

export interface PaginationMeta {
  page: number;
  limit: number;
  total: number;
}

export interface PaginatedResponse<T> {
  meta: PaginationMeta;
  data: Array<T>;
}
